import React from "react";

import Typography from "@material-ui/core/Typography";
import Container from "@material-ui/core/Container";
import bgImedg from "./img/tail-top.gif";

export default function WrapContainer({ coponent }: any) {
  return (
    <React.Fragment>
      <div>
        <Container fixed>{coponent}</Container>
      </div>
    </React.Fragment>
  );
}
