import React from "react";
import imgFb from "./img/fb-footer.png";
import imgInst from "./img/instagram-footer.png";
import imgTelegram from "./img/footer-tg.png";
import imgYouTube from "./img/youtube.png";

import imgLifeSell from "./img/png-transparent-vodafone-ukraine-lifecell.png";
import imgKievSell from "./img/Kyivstar-blue.png";
import imgMtsesell from "./img/sticker-png-mts-logo-mobile-operator.png";

import "./footer.scss";

function FooterSite() {
  return (
    <>
      <footer className="foter-site">
        <div className="tel-footer-info">
          <div className="tel-sell">
            <img src={imgLifeSell} alt="imgLifesell" className="icon-size" />
            <h3 className="tel-tex">+38 (093) 567-55-77</h3>
          </div>
          <div className="tel-sell">
            <img src={imgKievSell} alt="imgKievSell" className="icon-size" />
            <h3 className="tel-tex">+38 (097) 567-55-77</h3>
          </div>
          <div className="tel-sell">
            <img src={imgMtsesell} alt="imgMtsesell" className="icon-size" />
            <h3 className="tel-tex">+38 (099) 567-55-77</h3>
          </div>
        </div>
        <div className="footer-info">
          <a href="/" title="Мы на Facebook">
            <img src={imgFb} alt="imgFb" className="icon-size img-icon-mr" />
          </a>
          <a href="/" title="Мы в Instagram">
            <img
              src={imgInst}
              alt="imgInst"
              className="icon-size img-icon-mr"
            />
          </a>
          <a href="/" title="Наш канал в Telegram">
            <img
              src={imgTelegram}
              alt="imgTelegram"
              className="icon-size img-icon-mr"
            />
          </a>
          <a href="/" title="Наш канал на YouTube">
            <img
              src={imgYouTube}
              alt="imgYouTube"
              className="icon-size img-icon-mr"
            />
          </a>
        </div>
        <div className="text-copy">
          Copyright © 2007 - 2021 <br></br>
          <a href="/" className="hrew-text-copy">
            www.tropicalfishStore.org.ua
          </a>
          <br></br> Все права защищены.
        </div>
      </footer>
    </>
  );
}

export default FooterSite;
