import React from "react";

import HeadarBar from "./headar";
import NavRight from "./nav-right";
import "./App.scss";

import { BrowserRouter as Router, Switch, Route } from "react-router-dom";

import WrapContainer from "./wrap-container";

import HomeShopPg from "./home-shop-pg";

import FooterSite from "./footer";
import bgBoton from "./img/Untitled12.png";

function App() {
  return (
    <Router>
      <div
        className="app"
        style={{
          minHeight: "1500px",
          background: `url(${bgBoton}) no-repeat `,
          backgroundPosition: "50% 50%",
          backgroundSize: "cover",
        }}
      >
        <HeadarBar />
        <Switch>
          <Route path="/about">
            <About />
          </Route>
          <Route path="/users">
            <Users />
          </Route>
          <Route path="/">
            <Home />
          </Route>
        </Switch>
        <NavRight />
        <FooterSite />
      </div>
    </Router>
  );
}

function Home() {
  return (
    <>
      <WrapContainer coponent={HomeShopPg} />
    </>
  );
}

function About() {
  return <h2>About</h2>;
}

function Users() {
  return <h2>Users</h2>;
}

export default App;
