import React from "react";

import Button from "@material-ui/core/Button";

import AddShoppingCartIcon from "@material-ui/icons/AddShoppingCart";

import { fade, withStyles, makeStyles } from "@material-ui/core/styles";
import InputBase from "@material-ui/core/InputBase";
// import InputLabel from "@material-ui/core/InputLabel";

import FormControl from "@material-ui/core/FormControl";

import "./item-product.scss";
const useStyles = makeStyles((theme) => ({
  button: {
    border: "1px solid #fff",
    color: "#fff",
    "&:focus": {
      boxShadow: `${fade("#ffffff", 0.55)} 0 0 0 0.3rem`,
      borderColor: "white",
    },
  },
}));

const BootstrapInput = withStyles((theme) => ({
  root: {
    "label + &": {
      marginTop: theme.spacing(3),
    },
  },

  input: {
    borderRadius: 4,
    position: "relative",
    color: "white",
    border: "1px solid #fff",
    fontSize: 16,
    width: "50px",
    padding: "10px 12px",
    transition: theme.transitions.create(["border-color", "box-shadow"]),
    // Use the system font instead of the default Roboto font.
    fontFamily: [
      "-apple-system",
      "BlinkMacSystemFont",
      '"Segoe UI"',
      "Roboto",
      '"Helvetica Neue"',
      "Arial",
      "sans-serif",
      '"Apple Color Emoji"',
      '"Segoe UI Emoji"',
      '"Segoe UI Symbol"',
    ].join(","),
    "&:focus": {
      boxShadow: `${fade("#ffffff", 0.55)} 0 0 0 0.3rem`,
      borderColor: "white",
    },
  },
}))(InputBase);

export default function ItemProduct({
  imgItem,
  nameProd,
  textProd,
  priceProd,
}: any) {
  const classes = useStyles();
  return (
    <>
      <section className="body-product">
        <div className="img-product">
          <img src={imgItem} alt="imgProduct" />
        </div>
        <div className="title-ptoduct">
          <h2 className="name-prod">{nameProd}</h2>
          <p className="text-prod">{textProd}</p>
        </div>
        <div className="price-prod">
          <div className="price">
            {priceProd}
            <p> {".  "}Грн X</p>
          </div>
          <div className="input-cont">
            <FormControl>
              {/* <InputLabel shrink htmlFor="bootstrap-input">
                Клькість шт
              </InputLabel> */}
              <BootstrapInput
                defaultValue="0"
                id="bootstrap-input"
                type="number"
              />
            </FormControl>
          </div>
          <div className="botton-carts">
            <Button
              variant="outlined"
              size="large"
              color="primary"
              className={classes.button}
              startIcon={<AddShoppingCartIcon />}
            >
              Додати
            </Button>
          </div>
        </div>
      </section>
    </>
  );
}
