import React from "react";
import clsx from "clsx";
import { makeStyles } from "@material-ui/core/styles";
import SwipeableDrawer from "@material-ui/core/SwipeableDrawer";
import Button from "@material-ui/core/Button";
import List from "@material-ui/core/List";

import ListItem from "@material-ui/core/ListItem";
import ListItemIcon from "@material-ui/core/ListItemIcon";
import ListItemText from "@material-ui/core/ListItemText";

import MenuIcon from "@material-ui/icons/Menu";
import AddShoppingCartOutlinedIcon from "@material-ui/icons/AddShoppingCartOutlined";
import PinDropOutlinedIcon from "@material-ui/icons/PinDropOutlined";
import HouseOutlinedIcon from "@material-ui/icons/HouseOutlined";
import TransferWithinAStationOutlinedIcon from "@material-ui/icons/TransferWithinAStationOutlined";
import LocalMallOutlinedIcon from "@material-ui/icons/LocalMallOutlined";
import LocalShippingOutlinedIcon from "@material-ui/icons/LocalShippingOutlined";

import { Link } from "react-router-dom";

const useStyles = makeStyles({
  itemList: {},
  link: {
    marginLeft: "30px",
    textDecoration: "none",
    color: "rgb(0, 159, 227)",
  },
  list: {
    width: 250,
  },
  fullList: {
    width: "auto",
  },
});

type Anchor = "top" | "left" | "bottom" | "right";

export default function DrawerMany() {
  const classes = useStyles();
  const [state, setState] = React.useState({
    top: false,
    left: false,
    bottom: false,
    right: false,
  });

  const toggleDrawer = (anchor: Anchor, open: boolean) => (
    event: React.KeyboardEvent | React.MouseEvent
  ) => {
    if (
      event &&
      event.type === "keydown" &&
      ((event as React.KeyboardEvent).key === "Tab" ||
        (event as React.KeyboardEvent).key === "Shift")
    ) {
      return;
    }

    setState({ ...state, [anchor]: open });
  };

  const list = (anchor: Anchor) => (
    <div
      className={clsx(classes.list, {
        [classes.fullList]: anchor === "top" || anchor === "bottom",
      })}
      role="presentation"
      onClick={toggleDrawer(anchor, false)}
      onKeyDown={toggleDrawer(anchor, false)}
    >
      <List>
        <Link to="/" className={classes.link}>
          <ListItem button key="first" className={classes.itemList}>
            <ListItemIcon>
              <HouseOutlinedIcon className={classes.link} />
              <ListItemText
                primary="Головна сторінка"
                className={classes.link}
              />
            </ListItemIcon>
          </ListItem>
        </Link>

        <Link to="/shop" className={classes.link}>
          <ListItem button key="first" className={classes.itemList}>
            <ListItemIcon>
              <AddShoppingCartOutlinedIcon className={classes.link} />
              <ListItemText primary="Замовити товар" className={classes.link} />
            </ListItemIcon>
          </ListItem>
        </Link>

        <Link to="/about" className={classes.link}>
          <ListItem button key="tow" className={classes.itemList}>
            <ListItemIcon>
              <PinDropOutlinedIcon className={classes.link} />
              <ListItemText primary="Контакти" className={classes.link} />
            </ListItemIcon>
          </ListItem>
        </Link>

        <Link to="/users" className={classes.link}>
          <ListItem button key="free" className={classes.itemList}>
            <ListItemIcon>
              <TransferWithinAStationOutlinedIcon className={classes.link} />
              <ListItemText primary="Послуги" className={classes.link} />
            </ListItemIcon>
          </ListItem>
        </Link>
        <Link to="/users" className={classes.link}>
          <ListItem button key="free" className={classes.itemList}>
            <ListItemIcon>
              <LocalMallOutlinedIcon className={classes.link} />
              <ListItemText
                primary="Оптовим клієнтам"
                className={classes.link}
              />
            </ListItemIcon>
          </ListItem>
        </Link>
        <Link to="/users" className={classes.link}>
          <ListItem button key="free" className={classes.itemList}>
            <ListItemIcon>
              <LocalShippingOutlinedIcon className={classes.link} />
              <ListItemText
                primary=" Оплата і доставка"
                className={classes.link}
              />
            </ListItemIcon>
          </ListItem>
        </Link>
      </List>
    </div>
  );

  return (
    <div>
      {(["top"] as Anchor[]).map((anchor) => (
        <React.Fragment key={anchor}>
          <Button onClick={toggleDrawer(anchor, true)} color="inherit">
            <MenuIcon />
          </Button>
          <SwipeableDrawer
            anchor={anchor}
            open={state[anchor]}
            onClose={toggleDrawer(anchor, false)}
            onOpen={toggleDrawer(anchor, true)}
          >
            {list("top")}
          </SwipeableDrawer>
        </React.Fragment>
      ))}
    </div>
  );
}
